.menuwrap { position: fixed; background-color: white; background-color: rgba(255, 255, 255, 0.9); min-width: 540px; left: 50%; margin-left: -270px; min-height: 51px; border-bottom-left-radius: 8px; border-bottom-right-radius: 8px; z-index: 99999; text-align: center; transition: top 0.5s; -webkit-transition: top 0.5s; /* Safari */ transition-delay: 1s; -webkit-transition-delay: 1s; /* Safari */ }

#titlechart .menuwrap { position: absolute; z-index: inherit; background-color: transparent; }

