#secondary { background-color: white; background: -webkit-linear-gradient(120deg, rgba(255, 255, 255, 0.5) 49.83%, #cccccc 100%); background: -webkit-gradient(linear, 79.31% 115.39%, 20.69% -15.39%, color-stop(0.4983, rgba(255, 255, 255, 0.5)), color-stop(1, #cccccc)); background: -ms-linear-gradient(120deg, rgba(255, 255, 255, 0.5) 49.83%, #cccccc 100%); -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFF', endColorstr='#CCCCCC' ,GradientType=0)"; background: linear-gradient(-30deg, rgba(255, 255, 255, 0.5) 49.83%, #cccccc 100%); filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFF',endColorstr='#CCCCCC' , GradientType=1); }

#secondary .widget { width: 45%; display: block; float: left; padding-left: 2.5%; padding-right: 2.5%; }

#secondary .widget:nth-child(1) { width: 25%; }

#secondary .widget:nth-child(2) { width: 65%; }

#secondary section { padding-top: 2rem; padding-bottom: 2rem; }

#secondary input[type="search"] { margin-bottom: 1rem; }

#secondary input[type="submit"] { font-size: 65%; }

.search section { padding-top: 0; padding-bottom: 0; }

.search section .typography { padding-top: 10%; width: 90%; }

.cat-links, .tags-links { margin-right: 1rem; }

section.case_study.shelterwood .quote { position: absolute; width: 50%; right: 5%; top: 20%; background: transparent; }

@media only screen and (min-width: 641px) { section.case_study.shelterwood .quote { top: 30%; right: 10%; width: 40%; } }

section.case_study.men-of-iron .quote, section.case_study.max-lucado .quote { position: absolute; bottom: 0; left: 0; right: 0; width: 100%; padding: 1rem; }

@media only screen and (min-width: 641px) { section.case_study.men-of-iron .quote, section.case_study.max-lucado .quote { width: 80%; padding: 2rem; } }

section.case_study.men-of-iron .client-logo, section.case_study.max-lucado .client-logo { width: 30%; margin-top: -3%; margin-bottom: -5%; left: -.5rem; }

@media only screen and (min-width: 641px) { section.case_study.men-of-iron .client-logo, section.case_study.max-lucado .client-logo { left: -1rem; } }

